[class*=shj-lang-] {
  white-space: pre;
  color: #112;
  text-shadow: none;
  box-sizing: border-box;
  background: #fff;
  max-width: min(100%, 100vw);
  margin: 0;
  padding: 30px 20px;
  font: 18px/24px Consolas, Courier New, Monaco, Andale Mono, Ubuntu Mono, monospace;
}

.shj-inline {
  border-radius: 5px;
  margin: 0;
  padding: 2px 5px;
  display: inline-block
}

[class*=shj-lang-]::selection {
  background: #bdf5
}

[class*=shj-lang-] ::selection {
  background: #bdf5
}

[class*=shj-lang-]>div {
  display: flex;
  overflow: auto
}

[class*=shj-lang-]>div :last-child {
  outline: none;
  flex: 1
}

.shj-numbers {
  counter-reset: line;
  padding-left: 5px
}

.shj-numbers div {
  padding-right: 5px
}

.shj-numbers div:before {
  color: #999;
  content: counter(line);
  opacity: .5;
  text-align: right;
  counter-increment: line;
  margin-right: 5px;
  display: block
}

.shj-syn-cmnt {
  font-style: italic
}

.shj-syn-err,
.shj-syn-kwd {
  color: #e16
}

.shj-syn-num,
.shj-syn-class {
  color: #f60
}

.shj-syn-insert,
.shj-syn-str {
  color: #7d8
}

.shj-syn-type,
.shj-syn-oper {
  color: #5af
}

.shj-syn-section,
.shj-syn-func {
  color: #84f
}

.shj-syn-deleted,
.shj-syn-var {
  color: #f44
}

.shj-oneline {
  padding: 12px 10px
}

.shj-lang-http.shj-oneline .shj-syn-kwd {
  color: #fff;
  background: #25f;
  border-radius: 5px;
  padding: 5px 7px
}

[class*=shj-lang-] {
  color: #d4d4d4;
  background: #1e1e1e
}

[class*=shj-lang-]:before {
  color: #6f9aff
}

.shj-syn-insert {
  color: #98c379
}

.shj-syn-var {
  color: #9cdcfe
}

.shj-syn-oper {
  color: #d4d4d4
}

.shj-syn-num,
.shj-syn-kwd {
  color: #569cd6
}

.shj-syn-section,
.shj-syn-type,
.shj-syn-class {
  color: #4fc1ff
}

.shj-numbers,
.shj-syn-cmnt {
  color: #6a9955
}

.shj-syn-bool {
  color: #b5cea8
}

.shj-syn-deleted,
.shj-syn-err,
.shj-syn-str {
  color: #ce9178
}

.shj-syn-func {
  color: #dcdcaa
}